<template>
    <div class="indicator">
        <div class="indicator__value indicator__value_type_max" />
        <div
            class="indicator__value indicator__value_type_current"
            :style="getWidth"
        />
    </div>
</template>

<script>
export default {
    props: {
        value: {
            type: Number,
            required: true,
        },

        maxValue: {
            type: Number,
            required: true,
        },
    },

    computed: {
        getWidth() {
            const width = Math.round((this.value / this.maxValue) * 100);
            return `width: ${width}%;`;
        },
    },
};
</script>

<style lang="css" scoped>
.indicator {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 3px;
}

.indicator__value {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;

    border-radius: 1.5px;

    transition: width 0.2s ease;
}

.indicator__value_type_max {
    background-color: #272727;
}

.indicator__value_type_current {
    background-color: #0091ff;
}
</style>
