<template>
    <div class="loading">
        <img
            src="~@/assets/images/hero/hero.png"
            alt="Loading"
            class="loading__image"
        />

        <h1 class="loading__title">Загрузка...</h1>

        <p class="loading__description">Точим ножи, меняем конфигурации</p>

        <v-indicator
            class="loading__indicator"
            :value="value"
            :max-value="100"
        />
    </div>
</template>

<script>
import VIndicator from '@/components/VIndicator.vue';

export default {
    components: { VIndicator },
    data() {
        return {
            value: 10,
        };
    },

    mounted() {
        setInterval(() => {
            if (this.value >= 100) {
                this.value = 0;
            } else {
                this.value += 10;
            }
        }, 1000);
    },
};
</script>

